import uniStatusBar from "../../../component/index/uni-status-bar.vue"
import sMineThirdLogin from "../../../component/mine/s-mine-third-login.vue"
export default {
	components: {
		uniStatusBar,
		sMineThirdLogin
	},
	data() {
		return {
			statuesColor: "#ffe933",
			accountLogin: false,
			codeTime: 0,
			btnDisable: true,
			btnClick: false,
			phoneNum: "",
			authCode: "",
			accountNum: "",
			pwdNum: "",
			providerList: [],
			loginHint: "账号密码登录"
		}
	},
	onLoad() {
		this.getLoginChannel()
	},
	methods: {
		handleClose() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleSwitchLoginType() {
			this.accountLogin = !this.accountLogin
			if (this.accountLogin) {
				this.loginHint = "短信验证码登录"
			} else {
				this.loginHint = "账号密码登录"
			}
			this.phoneNum = ""
			this.authCode = ""
			this.accountNum = ""
			this.pwdNum = ""
		},
		handleGetMegCode() {
			if (!this.isValidPhone()) {
				uni.showToast({
					title: "请输入合法的手机号",
					icon: "none"
				})
				return;
			}
			if (this.codeTime > 0) {
				return;
			}
			this.codeTime = 60
			var timer = setInterval(() => {
				this.codeTime--
				if (this.codeTime < 1) {
					clearInterval(timer)
				}
			}, 1000)
			this.getMsgCode()
		},
		getMsgCode() {
			this.request({
				url: this.config.BASE_URL + "user/sendcode",
				method: "post",
				data: {
					phone: this.phoneNum
				}
			}).then(res => {
				this.authCode = res.msg.substr(4, res.msg.length - 1)
				uni.showToast({
					title: this.authCode,
					icon: "none"
				})
			})
		},
		handleLogin() {
			if (this.accountLogin) {
				this.handleAccountLogin()
			} else {
				this.handleAuthCodeLogin()
			}
		},
		handleAuthCodeLogin() {
			this.request({
				url: this.config.BASE_URL + "user/phonelogin",
				method: "post",
				data: {
					phone: this.phoneNum,
					code: this.authCode
				}
			}).then(res => {
				uni.setStorage({
					key: "userinfo",
					data: JSON.stringify(res.data),
					success() {
						uni.navigateBack({
							delta: 1
						})
					}
				})
			})
		},
		handleAccountLogin() {
			this.request({
				url: this.config.BASE_URL + "user/login",
				method: "post",
				data: {
					username: this.accountNum,
					password: this.pwdNum
				}
			}).then(res => {
				uni.setStorage({
					key: "userinfo",
					data: JSON.stringify(res.data),
					success() {
						uni.navigateBack({
							delta: 1
						})
					}
				})
			})
		},
		isValidPhone() {
			if (!(/^1[34578]\d{9}$/.test(this.phoneNum))) {
				return false
			} else {
				return true
			}
		},
		getLoginChannel() {
			uni.getProvider({
				service: 'oauth',
				success: (result) => {
					this.providerList = result.provider.map((value) => {
						let providerName = '';
						let icon = ''
						switch (value) {
							case 'weixin':
								providerName = '微信登录'
								icon = 'weixin'
								break;
							case 'qq':
								providerName = 'QQ登录'
								icon = 'QQ'
								break;
							case 'sinaweibo':
								providerName = '新浪微博登录'
								icon = 'xinlangweibo'
								break;
						}
						return {
							name: providerName,
							id: value,
							loginIcon: icon
						}
					});
				},
				fail: (error) => {
					console.log('获取登录通道失败', error);
				}
			});
		}
	},
	watch: {
		phoneNum(val) {
			if (this.phoneNum && this.phoneNum != null && this.authCode && this.authCode != null) {
				this.btnDisable = false
				this.btnClick = true
			} else {
				this.btnDisable = true
				this.btnClick = false
			}
		},
		authCode(val) {
			if (this.phoneNum && this.phoneNum != null && this.authCode && this.authCode != null) {
				this.btnDisable = false
				this.btnClick = true
			} else {
				this.btnDisable = true
				this.btnClick = false
			}
		},
		accountNum(val) {
			if (this.accountNum && this.accountNum != null && this.pwdNum && this.pwdNum != null) {
				this.btnDisable = false
				this.btnClick = true
			} else {
				this.btnDisable = true
				this.btnClick = false
			}
		},
		pwdNum(val) {
			if (this.accountNum && this.accountNum != null && this.pwdNum && this.pwdNum != null) {
				this.btnDisable = false
				this.btnClick = true
			} else {
				this.btnDisable = true
				this.btnClick = false
			}
		}
	}
}
